body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    margin-left: 20px;
    position: relative;
    background-color: #2c2c2c;
    color: white;
    
  }

  .info-status{
    text-align: center;
    margin-top: 10px;
    /* padding-left: 100px; */
  }

  li{
    /* display: flex; */
    /* border: 2px solid rebeccapurple; */
    margin: 5px;
    position: relative;
    /* background-color: #2c2c2c; */
    padding: 2px;
    /* list-style-position: inside; */
    
    color: white;
   
  }

  li:nth-child(1)
  {
    /* transform: scale(2); */
  }

  li:last-child .new{
    width: 300px;
    height: 50px;
  }
  .new{
    background-color: blue;
    color: white;
    border-radius: 15px;
    padding: 5px;
  }
  .new:hover{
    /* width: 10px; */
    /* display: block; */
    /* transform-origin: 0% 0%; */
    /* transform: scale(1.5); */
    width: 300px;
    height: 50px;
    cursor: pointer;
  }

  .res{
    background-color: rgb(252, 23, 92);
    margin: 10px auto;
    width: 100px;
    border-radius: 10px;
    padding: 5px;
    cursor: pointer;
  }
  
  @media only screen and (max-width: 600px) {
    body {
      background-color: lightblue;
    }
    .game {
      display: flex;
      /* flex-direction: row; */
      flex-direction: column;
    }
    .game-info {
      margin-left: 0px;
      position: relative;
      background-color: #2c2c2c;
      color: white;
      
    }
    .game-board{
      /* margin: auto; */
      transform-origin: 0% 0%;
      margin-bottom: 150px;
      margin-left: 15vw;
      transform: scale(2.2);

    }
  }